body{
  background: rgb(3, 0, 52);

min-height: 100vh;
}
h3{
  color: #bababa;
}
a{
  color: #ffffff;
  text-decoration: none;
}
p{
  color: white;
  font-size: 16px;
}
.container{
  max-width: 900px;
  margin: 50px  auto;
  padding: 0 30px;
}
header{
  position: sticky;
  top: 0;
  width: 100%;
  background: rgb(3, 0, 52);
}
table {
  table-layout: auto;
  width: 90%;
  border-collapse: collapse;
  border-spacing: 1px;
  text-align: left;
  margin: 50px auto;
  font-size: clamp(10px, 2vw, 16px);

}
thead{
  border-radius: 6px;
}
th {

  color: #bababa;
  padding: 10px 5px;
}
td {
  border-top: 0.5px solid #5c5c5c;
  color: #ffffff;
  font-weight: 500;
  padding: 20px 5px;
}
.list:hover{
  background-color: rgba(149, 149, 149, 0.3);
  cursor: pointer;

}
td img{
  width: clamp(15px, 2vw, 30px);
}
td div{
  display: flex;
  align-items: center;
  gap: 10px;
}
.circle{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid;
  border-top: none;
  animation-name: loading;
  transition: .45s ease;
  animation-duration: 4s;

}
.loading{
  margin: 0 auto;
  display: flex;
  justify-content: center;
  margin-top: 100px;
}
@keyframes loading{
  from{transform: rotate(0 deg);}
  to{transform: rotate(360deg);}
}
.inputDiv{
  width: 70%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  position: relative;
}
.inputDiv input{
  width: 100%;
  height: 35px;
  border: none;
  border-bottom: 1px solid rgb(255, 255, 255);
  padding: 0 10px;
  background: transparent;
  color: white;
}
.inputDiv input:focus{
  outline: none;
}
.searchIcon{
  position: absolute;
  right: 1%;
  top: 7px;
  color: white;
}
.negative{
  color: red;
}
.positive{
  color: rgb(0, 165, 0);
}
.priceChange{
  display: flex;
  align-items: center;
  gap: 0;
}
.pagination{
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
button{
  color: #ffffff !important;
}
.selectValue{
  background: transparent;
  color: #e7e7e7;
  padding: 3px 4px;
  border-radius: 6px;
  border: 1px solid #e7e7e7;
  cursor: pointer;
}
.selectValue option{
  background-color: #202020;
  border: none;
  cursor: pointer;
}
.selectValue option:hover{
  background-color: #5c5c5c !important;
}

.errorText{
  text-align: center;
  margin-top: 30px;
  
}
.title div{
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}
.liveChart{
  margin-top: 40px;
}